.theme-switch__control {
  width: 32px;
  height: 16px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.theme-switch__toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: #0000;
  -webkit-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #333;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  width: 13px;
  height: 13px;
  z-index: 2;
  pointer-events: none;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .3s;
  position: relative;
  transform: translateX(3px);
  box-shadow: 0 1px 1px #00000040;
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #bc0c0c8c;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transition: transform .3s;
  transform: translateX(17px);
}

